import { quanLyNguoiDungService } from "../../services/QuanLyNguoiDung";
import { DANG_NHAP_ACTION, LIST_USER, SET_THONG_TIN_NGUOI_DUNG } from "./types/QuanLyNguoiDungType";
import { history } from '../../App';
export const dangNhapAction = (thongTinDangNhap) => {
    return async (dispatch) => {
        try {
            const result = await quanLyNguoiDungService.dangNhap(thongTinDangNhap);
            if (result.data.statusCode === 200) {
                dispatch({
                    type: DANG_NHAP_ACTION,
                    thongTinDangNhap: result.data.content
                });
                //Chuyn hng ng nhp v admin
                history.push("/admin");
            }
        } catch (error) {
            console.log('error', error)
        }
    }
}
export const dangKyAction = (thongTinDangKy) => {
    return async (dispatch) => {
        try {
            const result = await quanLyNguoiDungService.dangKy(thongTinDangKy)
            if (result.data.statusCode === 200) {
                //Chuyn hng ng nhp v trang trc 
                history.goBack();
            }
            alert("ng k thnh cng !")
        } catch (error) {
            alert("ng k tht bi,th li sau !")
            console.log('error', error)
        }
    }
}
export const layThongTinNguoiDungAction = () => {
    return async (dispatch) => {
        try {
            const result = await quanLyNguoiDungService.layThongTinNguoiDung()
            if (result.data.statusCode === 200) {
                dispatch({
                    type: SET_THONG_TIN_NGUOI_DUNG,
                    thongTinNguoiDung: result.data.content
                });
            }
        } catch (error) {
            console.log('error', error.response.data)
        }
    }
}
export const layDanhSachNguoiDung = () => {
    return async (dispatch) => {
        try {
            const result = await quanLyNguoiDungService.layDanhSachNguoiDung()
            if (result.data.statusCode === 200) {
                dispatch({
                    type: LIST_USER,
                    listUser: result.data.content
                })
            }
        } catch (error) {
            console.log('error', error)
        }
    }
}
export const capNhatThongTinNguoiDung = (userEdit) => {
    return async (dispatch) => {
        try {
            const result = await quanLyNguoiDungService.capNhatThongTinNguoiDung(userEdit)
            alert("update thnh cng !")
            history.push("/admin")
            dispatch(layDanhSachNguoiDung())
        } catch (error) {
            alert("update tht bi !")
            console.log('error', error)
        }
    }
}
export const xoaNguoiDung = (account) => {
    return async (dispatch) => {
        try {
            const result = await quanLyNguoiDungService.xoaNguoiDung(account)
            alert("Xa thnh cng !")
            //console.log(result.data.content);
            dispatch(layDanhSachNguoiDung())
        } catch (error) {
            alert("Xa tht bi !")
            console.log('error', error)
        }
    }
}